import React from 'react'
import PropTypes from 'prop-types'
import Helmet from 'react-helmet'
import { StaticQuery, graphql } from 'gatsby'
import { Box, Flex, Image, Text } from "rebass"
import { setConfig } from 'react-hot-loader';

import Logo from '../images/logo.svg'
import '../styles/fonts.css'
import '../styles/global.css'
import './layout.css'

// Doing this here because we want this run at script start-up every time and
// this is the only file that is (at least for now) used by every page.
//  - Rob Nagle (2018-11-29)
setConfig({ pureSFC: true });

const hideOnMobile = `
  display: none;
  @media (min-width: 600px) {
    display: block;
  }
`
const Layout = ({ children }) => (
  <StaticQuery
    query={graphql`
      query SiteTitleQuery {
        site {
          siteMetadata {
            title
          }
        }
      }
    `}
    render={data => (
        <div>
          <Helmet
            title={data.site.siteMetadata.title}
            meta={[
              { name: 'description', content: 'Founded in 2012, Bitmatica is a small team of designers and engineers building web and mobile products for companies worldwide.' },
            ]}
          >
            <html lang="en" />
            <link href="https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800" rel="stylesheet" />
          </Helmet>

          <Box>
            {children}

            <Flex
              flexDirection={['column', 'row']}
              justifyContent="space-between"
              alignItems={['start', 'center']}
              mt="8rem"
              py={'2rem'}
              px={['2rem', 0]}
              mx="auto"
              css="max-width: 1080px; border-top: 1px solid #ccc;">

              <Image src={Logo} width="7em" mb={['1rem', 0]} />

              <Flex flexDirection={['column', 'row']} css="max-width: 1080px;">
                <Text fontSize={['14px']} className="test">hello@bitmatica.com</Text>
                <Text fontSize={['14px']} mx="0.5rem" css={hideOnMobile}>|</Text>
                <Text fontSize={['14px']}>+1 415 625 0615</Text>
                <Text fontSize={['14px']} mx="0.5rem" css={hideOnMobile}>|</Text>
                <Text fontSize={['14px']}>535 Mission St. 17th Floor, San Francisco, CA 94105</Text>
              </Flex>
            </Flex>
          </Box>
        </div>
    )}
  />
)

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
